<template>
  <div class="layouts">
    <Layout>
      <Sider class="side-wrap">
        <h3 class="site-name">上海学前教育监测数据系统平台</h3>
        <Menu theme="dark" @on-select="toPath" width="auto" :active-name="menuActiveNames" :open-names="subMenuActive" :accordion="true">
          <template v-for="item in menus">
            <Submenu v-if="item.children" :key="item.name" :name="item.name">
              <template slot="title">
                <Icon v-if="item.icon" :type="item.icon"></Icon>
                <span>{{item.title}}</span>
              </template>
              <MenuItem :name="children.name" v-for="children in item.children" :key="children.path">{{children.title}}</MenuItem>
            </Submenu>
            <MenuItem :name="item.name" :key="item.path" class='g-item-normal' v-if="!item.children">{{item.title}}</MenuItem>
          </template>
        </Menu>
        <div slot="trigger"></div>
      </Sider>
      <Layout>
          <Header class="layout-header-bar">
            <h2 class="breadcrumb">{{this.$route.name}}</h2>
            <Poptip class="avatar-wrap">
              <div style="cursor: pointer;"><Avatar  class="avatar" icon="person" /><span style="margin-left:10px;">{{currName}}</span> </div>
              <div slot="content" style="text-align:center;">
                <Button type="text" @click="gotoInfo" class="login-menu">个人中心</Button>
                <Button type="text" @click="logout" class="login-menu">退出登录</Button>
              </div>
            </Poptip>
          </Header>
          <Content class="content-wrap">
            <router-view/>
          </Content>
      </Layout>
  </Layout>
  </div>
</template>

<script>
  import Cookies from 'js-cookie'
  import { mapGetters } from 'vuex'
  export default {
    data() {
      return {
        menuActiveNames: '',
        subMenuActive: []
      }
    },
    created() {
      this.initMenuActiveName()
    },
    methods: {
      initMenuActiveName() {
        this.menuActiveNames = this.$route.name
        this.subMenuActive.splice(0, 1, this.$route.meta.parent)
      },
      toPath(name) {
        this.$router.push({
          name: name
        })
      },
      gotoInfo() {
        this.$router.push('/info')
      },
      logout() {
        this.$ajaxPost(this.projectName + '/staff/logout.do').then(res => {
          this.$Message.success('恭喜你，成功退出!');
          Cookies.remove('Temp-User');
          Cookies.remove('administratorFlag');
          Cookies.remove('U-ID');          
          this.$router.replace('/login');
        });
      }
    },
    computed: {
      ...mapGetters(['menus']),
      currName() {
        return JSON.parse(Cookies.get('Temp-User')).name;
      },
      isAdminFlag(){
        return !!(+(Cookies.get('administratorFlag'))==='1');
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/scss/core.scss';
  .login-menu {
    display: block;
    width: 100%;
    border: none;
    box-shadow: none;
  }
  .m-active {
    background: $menu-active-color !important;
    color: $text-color !important;
  }
  .site-name {
    padding: 30px 20px 40px 20px;
    color: #fff;
    text-align: center;
  }
  .layout-header-bar {
    margin: 10px 20px;
    background: #fff;
    border: 1px solid #eee;
  }
  .content-wrap {
    margin: 20px;
    background: #fff;
    min-height: 260px;
    box-shadow: 0 0 8px #ddd;
    padding: 20px;
    border-radius: 4px;
  } // layout
  .side-wrap {
    min-height: 100vh;
  }
  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .layout-header-bar {
    background: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .1);
  }
  .menu-item span {
    display: inline-block;
    overflow: hidden;
    width: 69px;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: bottom;
    transition: width .2s ease .2s;
  }
  .menu-item i {
    transform: translateX(0px);
    transition: font-size .2s ease, transform .2s ease;
    vertical-align: middle;
    font-size: 16px;
  }
  .collapsed-menu span {
    width: 0px;
    transition: width .2s ease;
  }
  .collapsed-menu i {
    transform: translateX(5px);
    transition: font-size .2s ease .2s, transform .2s ease .2s;
    vertical-align: middle;
    font-size: 22px;
  } // breadcrumb
  .breadcrumb {
    float: left;
  }
  .avatar-wrap {
    float: right;
  }
  .avatar {
    background: #2d8cf0;
  }
</style>



